button {
    font-size: 15px;
}

.container {
    background-color: lightsteelblue;
}

.displayedData .row {
    margin: 10px 0;
}

.displayedData .btn-group {
    float: right;
}

.displayedData [class^=col]:first-child {
    padding-left: 0;
}

.displayedData [class^=col]:last-child {
    padding-right: 0;
}

.displayController {
    margin-bottom: 10px;
}

form button {
    height: 34px;
    margin: 10px 0 10px 0;
}

input {
    width: 100%;
    height: 28px;
    margin: 10px 0;
}

.btn-group {
    margin: 0 auto;
}

h1 {
    text-align: center;
}




